define('opendata-ember/adapters/dataset', ['exports', 'ember', 'opendata-ember/adapters/application'], function (exports, Ember, ApplicationAdapter) {

  'use strict';

  exports['default'] = ApplicationAdapter['default'].extend({

    pathForType: function pathForType(type) {
      var camelized = Ember['default'].String.camelize(type);
      return Ember['default'].String.pluralize(camelized);
    },

    urlForFindRecord: function urlForFindRecord(id /*, modelName, snapshot*/) {
      Ember['default'].debug('Dataset Adapter: urlForFindRecord called id: ' + id);
      var host = this.get('host');
      var namespace = this.get('namespace');
      return host + '/' + namespace + '/datasets/' + id;
    },

    query: function query(store, type, _query) {
      Ember['default'].debug('>>>>> query');
      _query.filter = { source: 'ESRI R&D Center' };
      return this._super(store, type, _query);
    }

  });

});