define('opendata-ember/components/download-button', ['exports', 'ember', 'ember-data', 'opendata-ember/config/environment'], function (exports, Ember, DS, ENV) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({

    baseUrl: (function () {
      var model = this.get('model');
      var url = ENV['default'].APP.API;
      url += DS['default'].JSONAPIAdapter.prototype.buildURL('dataset', model.get('id'));
      return url;
    }).property('model.id')

  });

});