define('opendata-ember/router', ['exports', 'ember', 'opendata-ember/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    rootURL: config['default'].rootURL,
    location: config['default'].locationType
  });

  Router.map(function () {
    this.resource('datasets', function () {});
    this.resource('dataset', { path: '/datasets/:id' });
  });

  exports['default'] = Router;

});